*{
    scroll-behavior: smooth;
}

.homePage{
    margin: 0;
    padding: 0;
}

/*Header*/
.navbar{
    margin: 0 25px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo h1{
    color: #202225;
    font-size: 40px;
    font-weight: 400;
}

.logo span{
    color: rgb(60, 104, 207);
}

.parcourir ul{
    display: flex;
    list-style: none;
}

.parcourir li{
    cursor: pointer;
    margin: 0 20px;
}

.link__mod{
    font-size: 20px;
    text-decoration: none;
    color: #202225;
    transition: 0.5s;
    -webkit-transition: 0.5s;
    -moz-transition: 0.5s;
    -ms-transition: 0.5s;
    -o-transition: 0.5s;
}

.link__mod:hover{
    color: rgb(55, 49, 156);
    transition: 0.5s;
    -webkit-transition: 0.5s;
    -moz-transition: 0.5s;
    -ms-transition: 0.5s;
    -o-transition: 0.5s;
}

.link__btn{
    border: 1px solid #202225;
    background-color: #202225;
    padding: 15px 20px;
    text-decoration: none;
    color: #fff;
    font-weight: 600;
    transition: 0.5s;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    -webkit-transition: 0.5s;
    -moz-transition: 0.5s;
    -ms-transition: 0.5s;
    -o-transition: 0.5s;
}

.link__btn:hover{
    border: 1px solid rgb(55, 49, 156);
    background-color: #fff;
    font-weight: 600;
    color: rgb(55, 49, 156);
    transition: 0.5s;
    -webkit-transition: 0.5s;
    -moz-transition: 0.5s;
    -ms-transition: 0.5s;
    -o-transition: 0.5s;
}

.header-content{
    margin-top: 250px;
    font-size: 17.5px;
    color: #202225;
    text-align: center;
    height: 50vh;
}

.icon1{
    position: absolute;
    top: 150px;
    left: 10%;
    transform: rotate(-25deg);
    width: 75px;
    -webkit-transform: rotate(-25deg);
    -moz-transform: rotate(-25deg);
    -ms-transform: rotate(-25deg);
    -o-transform: rotate(-25deg);
}

.icon2{
    position: absolute;
    top: 125px;
    left: 50%;
    width: 80px;
}

.icon3{
    position: absolute;
    top: 175px;
    left: 90%;
    transform: rotate(25deg);
    width: 75px;
    -webkit-transform: rotate(25deg);
    -moz-transform: rotate(25deg);
    -ms-transform: rotate(25deg);
    -o-transform: rotate(25deg);
}

.icon4{
    position: absolute;
    top: 500px;
    left: 90%;
    transform: rotate(-25deg);
    width: 75px;
    -webkit-transform: rotate(-25deg);
    -moz-transform: rotate(-25deg);
    -ms-transform: rotate(-25deg);
    -o-transform: rotate(-25deg);
}

.icon5{
    position: absolute;
    top: 525px;
    left: 50%;
    width: 80px;
}

.icon6{
    position: absolute;
    top: 500px;
    left: 10%;
    transform: rotate(25deg);
    width: 75px;
    -webkit-transform: rotate(25deg);
    -moz-transform: rotate(25deg);
    -ms-transform: rotate(25deg);
    -o-transform: rotate(25deg);
}

/*Main*/
.titre__desc{
    font-size: 35px;
    font-weight: 300;
    text-align: center;
}

.description{
    height: 98vh;
}

.desc_content{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #202225;
    margin: 0 25px;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
}

.desc_left{
    width: 35%;
    color: #fff;
}

.desc_left p{
    letter-spacing: 1px;
    margin: 20px 15px 10px 15px;
    font-size: 17.5px;
}

.desc_left h2{
    font-size: 20px;
    font-weight: 300;
    margin: 20px 15px;
}

.desc_right{
    width: 75%;
    text-align: center;
}

.titre_right{
    color: #fff;
    font-size: 20px;
    font-weight: 300;
    margin: 20px 15px;
}

.appercu_img{
    margin-top: 10px;
    margin-bottom: 35px;
    border-radius: 2.5px;
    border: 1px solid #fff;
    width: 98%;
    -webkit-border-radius: 2.5px;
    -moz-border-radius: 2.5px;
    -ms-border-radius: 2.5px;
    -o-border-radius: 2.5px;
}

.contact{
    height: 100vh;
}

.titre_contact{
    font-size: 35px;
    font-weight: 300;
    text-align: center;
}

.contact_content{
    display: flex;
    align-items: center;
}

.contact_left{
    width:45%;
    margin-left: 2.5%;
}

.contact_img{
    width: 100%;
}

.contact_right{
    text-align: center;
    width: 45%;
    margin-left: 5%;
}

.contact_right h2{
    font-weight: 400;
}

/*Footer*/
